<template>
  <el-row>
    <el-col :span="24">
      <el-col :span="4" :offset="2">
        <div class="menu">
          <span>MENUS</span>
        </div>
        <div class="welcome">
          <ul>
            <li><a href="javascript:void(0)" @click="setWelcomePage"><span>Scientific committee</span></a></li>
            <li><a href="javascript:void(0)" @click="setMeetingTimePage"><span>Organizing Committee</span></a></li>
            <li><a href="javascript:void(0)" @click="setAttendantPage"><span>speakers</span></a></li>
            <li><a href="javascript:void(0)" @click="setMeetingPlacePage"><span>General information</span></a></li>
          </ul>
        </div>
      </el-col>
      <el-col :span="12" :offset="3">
        <div v-if="WelcomePage " class="welcom-content">
          <div class="content-titel">
            <h1>{{WelcomePageTitle}}</h1>
          </div>
          <div class="content-main">
            <span>{{WelcomePageContent}}</span>
          </div>
        </div>
        <div v-if="MeetingTimePage " class="welcom-content">
          <div class="content-titel">
            <h1>{{MeetingTimePageTitle}}</h1>
          </div>
          <div class="content-main">
            <span>{{MeetingTimePageContent}}</span>
          </div>
        </div>
        <div v-if="AttendantPage " class="welcom-content">
          <div class="content-titel">
            <h1>{{AttendantPageTitle}}</h1>
          </div>
          <div class="content-main">
            <span>{{AttendantPageContent}}</span>
          </div>
        </div>
        <div v-if="MeetingPlacePage " class="welcom-content">
          <div class="content-titel">
            <h1>{{MeetingPlacePageTitle}}</h1>
          </div>
          <div class="content-main">
            <span>{{MeetingPlacePageContent}}</span>
          </div>
        </div>
      </el-col>
    </el-col>
  </el-row>
</template>

<script>
  /**
   * Created by xzc
   * 描述:
   */
  export default {
    data() {
      return {
        WelcomePage: true,
        MeetingTimePage: false,
        AttendantPage: false,
        MeetingPlacePage: false,
        WelcomePageTitle: '欢迎各位同事参加',
        WelcomePageContent: 'Dear Colleagues and Friends,We are delighted to invite' +
        ' you to the 18th biennial ESID meeting to be held from 24-27 October,' +
        ' 2018, in Lisbon, Portugal. This meeting will be focusing on ' + 'From Fetus to Elderly It is being organized for you by the local and international scientific committee.The ESID 2018 meeting in Lisbon will encompass  the very latest exciting perspectives bridging  basic and clinical research across primary immunodeficiency and related infection, autoimmunity and inflammation. A very stimulating and innovative programme  brings together   a unique  faculty of  internationally  renowned clinicians and scientists to present and discuss significant and exciting developments in diagnostic immunology, genetics and immunobiology of primary immunodeficiencies, through keynote lectures, symposia,' +
        ' educational workshops, interactive sessions and meet-the-professor sessions.' +
        'The first day of the meeting will be dedicated to our  highly acclaimed educational programme with further sessions throughout the meeting with  world-class teachers bringing high quality educational lectures and interactive sessions which will be of great benefit to  students, trainees  and established clinicians and scientists with practical and relevant up-date  current insights into pathogenesis, recognition and management of immune disorders.We look forward to welcoming you to the beautiful and charming city of Lisbon, for an inspiring and cutting edge meeting bringing you the latest advances in the field.With very best wishes,',
        MeetingTimePageTitle: '会议时间',
        MeetingTimePageContent: '2018年5月5日至6日',
        AttendantPageTitle: '参会人员',
        AttendantPageContent: '凡是在会议开始之前注册报名的所有会员',
        MeetingPlacePageTitle: '会议地点',
        MeetingPlacePageContent: '重庆丽笙世嘉酒店  中国重庆南岸区南滨路22号'
      }
    },
    methods: {
      setWelcomePage() {
        this.MeetingTimePage = false
        this.AttendantPage = false
        this.MeetingPlacePage = false
        this.WelcomePage = true
      },
      setMeetingTimePage() {
        this.WelcomePage = false
        this.AttendantPage = false
        this.MeetingPlacePage = false
        this.MeetingTimePage = true
      },
      setAttendantPage() {
        this.WelcomePage = false
        this.MeetingTimePage = false
        this.MeetingPlacePage = false
        this.AttendantPage = true
      },
      setMeetingPlacePage() {
        this.WelcomePage = false
        this.MeetingTimePage = false
        this.AttendantPage = false
        this.MeetingPlacePage = true
      }
    }
  }
</script>

<style scoped>
  .menu {
    margin-top: 6rem;
