    height: 3rem;
    border-bottom: 0.2rem solid #f15a22;
  }

  .content-titel h1 {
    font-size: 1.4rem;
    line-height: 2rem;
    text-align: center;
    color: #f15a22;
  }

  .content-main {
    margin: 2rem;

  }

  .content-main span {
    font-size: 1.2rem;
    line-height: 1.5rem;
    text-align: center;
    color: #013763;
  }

</style>



